import { Component } from "react";
class DemoA extends Component{
  constructor(props) {
    super(props)
    this.state = {
      todo: '',
      list:[],
      checkVal:[],
      flag:false
    }
  }
  //input改变
  inputChange(e) {
    // console.log(e.target.value);
    this.setState({todo:e.target.value})
  }
  //add
  addTodo(e) {
    let { todo, list } = this.state
    let flag = list.find(item => item.task == todo)
    if (!todo.trim()) {
     alert('请输入内容')
      return
    } else if(flag){
      alert('请勿重复添加')
      this.setState(() => ({
        todo:''
      }))
       return
    }
       this.setState(() => ({
        list:[...list,{id:Date.now(),task:todo}]
      }))
    this.setState(() => ({
        todo:''
      }))
  }
  //inputKeyUp 回车键  添加
  inputKeyUp(e) {
    if (e.keyCode === 13) { 
      this.addTodo()
    }
  }
  //删除
  delTodo(id) {
    this.setState(() => ({
      list:this.state.list.filter(ele => ele.id != id)
    }))
  }
  //选择
 handelCheckbox(e){
          let val = e.target.value
          let arr = this.state.checkVal
          let bstop = arr.includes(val)
          if(bstop){
              let index = arr.indexOf(val)
              arr.splice(index,1)
          }else{
              arr.push(val)
          }
          this.setState({
              checkVal:arr
          },()=>{
            console.log(this.state.checkVal)
            // this.Content()
          })
     }
  // Content(task, id) {
  //   console.log(id);
  //   console.log(this.state.checkVal)
  //   let a =this.state.checkVal.filter(item => item.id == id)
  //    console.log(a);
  //   if(a){
  //       return(
  //       <span style={{ 'textDecoration': 'line-through' }}>{ task}</span>
  //     )
  //   }else {
  //      return(
  //       <span >{ task}</span>
  //     )
  //      }
     
  //    }
  render() {
    let { todo, list } = this.state
    return(
      <div className="content">
        <div className="herder">
          <input placeholder="请输入添加内容" onChange={(e)=>{this.inputChange(e)}} value={this.state.todo} onKeyUp={(e)=>{this.inputKeyUp(e)}} ></input>
          <button onClick={e=>this.addTodo(e)}>add</button>
        </div>
        <div className="main">
          {
            list.map(item => (
              // textDecoration:(this.state.checkVal.find(ele=>ele.id==item.id)?'line-through':'')
              <div className="text" key={item.id} >
                <input type="checkbox" value={item.id}  onChange={(e)=>{this.handelCheckbox(e)}}/>
                {/* <span>{item.id}</span> */}
                {/* text-decoration:'line-through' */}
                <span >{ item.task}</span>
                <div className="btn">
                  <button onClick={() => this.delTodo(item.id)}>删除</button>
                 {/* <button>编辑</button> */}
                </div>
              </div>
            ))
          }
        </div>
    </div >
  )
  }
}
export default DemoA